<template>
  <!-- Rooms -->
  <m-rooms
    :rooms="rooms"
  ></m-rooms>
</template>

<script>
export default {
  components: {
    mRooms: () => import('@/components/rooms')
  },
  data () {
    return {
      socket: this.$store.getters['Game/GetSocket'],
      rooms: []
    }
  },
  created () {
    this.$store.commit('Game/SetBackRoute', { name: 'main' })
  },
  mounted () {
    this.socket.emit('userConnected')
    this.socket.on('rooms', data => {
      // console.log('data ', data)
      this.rooms = data
    })
  }
}
</script>